import React from 'react';
import Fade from 'react-reveal/Fade';
import { Container, Row, Col } from 'react-bootstrap';
import './About.css';
import {NavBar} from '../NavBar/NavBar';


const About = () => {
    return (
        <section id="about">
          <NavBar />
      <Container>
        <Fade left duration={1000} delay={1000} distance="30px">
        <h1 className='header'>About Smite Society</h1>
        </Fade>
        <Row className="about-wrapper">
          <Col md={6} sm={12}>
          </Col>
          <Col md={6} sm={12}>
            <Fade left duration={1000} delay={1000} distance="30px">
              <div className="about-wrapper__info">
                <p className="about-wrapper__info-text">
                    Smite Society is a personal project page, devoted to an online video game called Smite. 
                    Smite is a MOBA (Massive/Multiplayer Online Battle Arena. The playable characters are based on 
                    mythological pantheons (ex. Zeus, Ra), with various different playable game modes.
                </p>
                <p className="about-wrapper__info-text">
                    This page is intended to either help you get better at the game and improve, 
                    or add some flavor/fun to your games with your friends with some random god/item builds.
                </p>
                <p className="about-wrapper__info-text">
                    The Randomizer page is all about fun. It will give you a number of god/item build combinations that you and your
                    friends can attempt to win with in-game. These builds should NOT be taken seriously and will most likely result in
                    a struggle to win.
                </p>
                <p className="about-wrapper__info-text">
                    The tierlist is a sortable, drag and drop page where you can arrange the gods into the tier that you see fit. This 
                    can be based off of any criteria you wish, such as YOUR personal favorite OR how strong you think that god is at the 
                    current moment.
                </p>
                <p className="about-wrapper__info-text">
                    I do have some other ideas to implement into the page, but for now this is all I have completely functional. The above
                    pages will also be touched up, for example adding a reset button to the tierlist page as well as an export function so that
                    you can show your friends your rankings.
                </p>
              </div>
            </Fade>
          </Col>
        </Row>
      </Container>
    </section>
    )

}

export default About;